import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'default',
      redirect: '/home'
    },
    //  首页
    {
      path: '/home',
      name: 'Home',
      // 路由懒加载
      component (resolve) {
        require(['../components/home.vue'], resolve)
      },
      // 配置是否需要keep-alive缓存
      meta: {
        needKeepAlive: true
      }
    },
    // 404
    {
      path: '*',
      redirect: '/home'
    }
  ]
})



